import React, { useState } from 'react'
import TodoList from './TodoList';

// class Form extends React.Component {
function Form(props) {
    // constructor(props) {
    //     super(props);
    //     this.state = {
    //         inputText: ""
    //     }
    //     this.handleInputChange = this.handleInputChange.bind(this);
    //     this.handleOnSubmit = this.handleOnSubmit.bind(this);
    // }

    const [inputText, setInputText] = useState("");

    const handleOnSubmit = (e) => {
        e.preventDefault();

        const duplicate = props.todoList.some(todo => todo.text === inputText);

        // const result = props.handleAdd(e, { text: inputText, done: false })
        if (duplicate) {
            alert('Ya existe el elemento');
        } else {
            // this.setState({ inputText: "" });
            props.handleAdd({ text: inputText, done: false });
            setInputText("");
        }
    }

    // handleInputChange(e) {
    const handleInputChange = (e) => {
        // this.setState({ inputText: e.target.value });
        setInputText(e.target.value);
    }
    // render() {
    // }
    return (
        <form className="form" onSubmit={handleOnSubmit}>
            <input type="text" onChange={handleInputChange} placeholder="Escribe aquÃ­" value={inputText} />
            <button type="submit" >Crear</button>
        </form>
    )
}

export default Form
