// import logo from './logo.svg';
import Header from "./components/Header";
import Form from './components/Form';
import './App.css';
import TodoList from "./components/TodoList";
import React, { useState } from 'react';


const initialValue = [
  { text: "Hacer ejercicio", done: false },
  { text: "Pasear a dubis", done: false },
  { text: "Leer el prework de BEDU :P", done: false },
  { text: "Escuchar un podcast", done: false }
]

// class App extends React.Component {
function App() {

  // constructor(props) {
  //   super(props);
  //   this.state = {
  //     todoList: []
  //   }
  // }
  const [todoList, setTodoList] = useState(initialValue);

  // componentDidMount() {
  //   this.setState();
  // }

  const handleToggle = (e, n) => {
    setTodoList(state => {
      return state.map((elem, index) => {
        if (index === n) {
          return {
            ...elem,
            done: !elem.done
          }
        }
        return elem;
      })
    })
  }
  const handleDelete = (e, n) => {
    setTodoList(todoList.filter((elem, index) => n !== index))
  }

  const handleAdd = (newTodo) => {
    setTodoList(state => {
      return [...state, newTodo]
      // if (!found) {
      // }
      // return {
      //   ...state,
      //   todoList: [...state.todoList, newTodo]
      // }
      // return state;
    });

  }

  // render() {

  // }
  return (
    <div className="App" >
      <Header />
      <Form todoList={todoList} handleAdd={handleAdd} />
      <TodoList todoList={todoList} handleToggle={handleToggle} handleDelete={handleDelete} />
    </div>
  );
}

export default App;
